import React, { useState } from 'react';
import { TypeAnimation } from 'react-type-animation';
import '../styles/MemoCard.scss';

const MemoCard = ({ memo, onFinished, onNext, onPrev, isFirst, isLast }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [showContent, setShowContent] = useState(false);

  console.log('Memo content:', memo); // 

  const handleOpen = () => {
    setIsOpen(true);
    setShowContent(true);
  };

  // 
  if (!memo) {
    return (
      <div className="memo-page">
        <div className="card-container">
          <div className="paper-wrapper">
            <div className="memo-content">
              <div className="typewriter-text">
                
              </div>
            </div>
          </div>
        </div>
        <div className="navigation-arrows">
          <button 
            className="nav-arrow" 
            onClick={onPrev} 
            disabled={isFirst}
          >
            
          </button>
          <button 
            className="nav-arrow" 
            onClick={onNext} 
            disabled={isLast}
          >
            
          </button>
        </div>
      </div>
    );
  }


  return (
    <div className="memo-page">
      <div className="card-container">
        <div className="paper-wrapper">
          {!isOpen ? (
            <button 
              className="open-button" 
              onClick={handleOpen}
            >
              
            </button>
          ) : (
            <div className="memo-content open">
              {showContent && (
                <TypeAnimation
                  sequence={[
                    memo + "by YY",
                    1000,
                    onFinished
                  ]}
                  wrapper="div"
                  speed={30}
                  className="typewriter-text"
                  cursor={true}
                  repeat={0}
                />
              )}
            </div>
          )}
        </div>
      </div>
      <div className="navigation-arrows">
        <button 
          className="nav-arrow" 
          onClick={onPrev} 
          disabled={isFirst}
        >
          
        </button>
        <button 
          className="nav-arrow" 
          onClick={onNext} 
          disabled={isLast}
        >
          
        </button>
      </div>
    </div>
  );
};

export default MemoCard; 