import React, { useState, useRef } from "react";
import { LuckyWheel } from "@lucky-canvas/react";
import { wheelItems } from "../data/memos";

export default function LuckyWheelComponent() {
  const [blocks] = useState([{ padding: "10px", background: "#869cfa" }]);
  const [prizes] = useState([
    {
      background: "#e9e8fe",
      fonts: [{ text: "", fontStyle: "Ma Shan Zheng", top: 10 }],
      range: 20,
    },
    {
      background: "#b8c5f2",
      fonts: [{ text: "", fontStyle: "Ma Shan Zheng", top: 10 }],
      range: 20,
    },
    {
      background: "#e9e8fe",
      fonts: [{ text: "", fontStyle: "Ma Shan Zheng", top: 10 }],
      range: 10,
    },
    {
      background: "#b8c5f2",
      fonts: [{ text: "", fontStyle: "Ma Shan Zheng", top: 10 }],
      range: 10,
    },
    {
      background: "#e9e8fe",
      fonts: [{ text: "", fontStyle: "Ma Shan Zheng", top: 10 }],
      range: 10,
    },
    {
      background: "#b8c5f2",
      fonts: [{ text: "", fontStyle: "Ma Shan Zheng", top: 10 }],
      range: 20,
    },
    {
      background: "#F1F3F4",
      fonts: [{ text: "", fontStyle: "Ma Shan Zheng", top: 10 }],
      range: 10,
    },
  ]);
  const [buttons] = useState([
    { radius: "40%", background: "#617df2" },
    { radius: "35%", background: "#afc8ff" },
    {
      radius: "30%",
      background: "#869cfa",
      pointer: true,
      fonts: [{ text: "", top: "-10px", fontStyle: "Ma Shan Zheng" }],
    },
  ]);
  const myLucky = useRef();
  return (
    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
      <LuckyWheel
        ref={myLucky}
        width="300px"
        height="300px"
        blocks={blocks}
        prizes={prizes}
        buttons={buttons}
        onStart={() => {
          // star
          myLucky.current.play();
          setTimeout(() => {
            const index = (Math.random() * 6) >> 0;
            myLucky.current.stop(index);
          }, 2500);
        }}
        onEnd={(prize) => {
          // end
          if (prize.fonts[0].text === "") {
            alert("");
          }

          if (prize.fonts[0].text === "") {
            alert("yy~");
          }
          alert(" " + prize.fonts[0].text + " yy~");
        }}
      />
    </div>
  );
}
